.roadmap-main{
    /* background: linear-gradient(0deg, rgba(118, 56, 233, 0.8), rgba(118, 56, 233, 0.8)); */
    height: 100%;
    
}


.roadmap-title{
    padding-top: 1.5em;
    font-family: 'Bowlby One SC', cursive;
}





  
  .bi {
    vertical-align: -.125em;
    fill: currentColor;
  }
  
  .opacity-50 { opacity: .5; }
  .opacity-75 { opacity: .75; }
  
  .list-group {
    width: auto;
    max-width: 700px;
    margin: 3rem auto;
  }
  
  .form-check-input:checked + .form-checked-content {
    opacity: .5;
  }
  
  .form-check-input-placeholder {
    pointer-events: none;
    border-style: dashed;
  }

  [contenteditable]:focus {
    outline: 0;
  }
  
  .list-group-checkable {
    display: grid;
    gap: .5rem;
    border: 0;
  }

  .roadmap-date{
      color:rgba(118, 56, 233, 0.8);
  }

  .list-group-checkable .list-group-item {
    border: 2px solid rgba(118, 56, 233, 0.8);
    border-radius: .5rem;
    margin: 0 15px;
    font-size: 20px;
  }

  .list-group-item-check {
    position: absolute;
    clip: rect(0, 0, 0, 0);
    pointer-events: none;
  }

  .list-group-item-check:hover + .list-group-item {
    background-color: var(--bs-light);
  }

  /* .list-group-item-check:checked + .list-group-item {
    color: #fff;
    background-color: var(--bs-blue);
  } */

  .list-group-item-check[disabled] + .list-group-item,
  .list-group-item-check:disabled + .list-group-item {
    pointer-events: none;
    filter: none;
    opacity: .5;
  }
  