
/* Custom default button */
.btn-secondary,
.btn-secondary:hover,
.btn-secondary:focus {
  color: #333;
  text-shadow: none; /* Prevent inheritance from `body` */
}

.main-heading{
font-family: 'Bowlby One SC', cursive;
}


 
.main-app{
  font-family: 'Outfit', sans-serif;
}


.cover-header{
  margin-top: 5em;
  
}

.cover-sub{
  margin-bottom: 2em;
}

.cover-button{
  margin-bottom: 9em;
}

.cover-app{
  /* background: linear-gradient(0deg, rgba(118, 56, 233, 0.8), rgba(118, 56, 233, 0.8)) */
  background: linear-gradient(0deg, rgba(118, 56, 233, 0.507), rgba(241, 65, 226, 0.555));
  /* background-image: url('bg.jpg'); */
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.cover-container {
  max-width: 70em;
}

.bd-placeholder-img {
  font-size: 1.125rem;
  text-anchor: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

@media (min-width: 768px) {
  .bd-placeholder-img-lg {
    font-size: 3.5rem;
  }
}